@import "./variables.scss";

body {
  margin: 0;
  padding: 0;
  // min-width: $window-min-width;
  font-family: "Lato-Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  color: $text-color;

  a {
    color: get-color($name: orange);
    text-decoration: none;
  }
}

.App {

  .main {
    height: calc(calc(var(--vh, 1vh) * 100) - #{$header-height});
    display: grid;
    grid-template-columns: $nav-closed-width 1fr;
    position: relative;
    overflow: hidden;

    .sections-wrapper {
      z-index: 2; // to get it on same plane as active nav-tab
      position: relative;
      height: inherit;
      overflow-y: scroll;
      overflow-x: hidden;
      -webkit-overflow-scrolling: touch;
      outline: none;
    }
  }

  // Intersection observer
  .TextCard .headline, 
  [class^="Vertical"] .bar-group, 
  .Horizontal .bar-measure {
    opacity: 0;
  }
  .inView.TextCard .headline {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp;
    animation-duration: $animation-duration;
    animation-delay: 0ms;
    animation-iteration-count: 1;
    opacity: 1;
  }

  .inView [class^="Vertical"] .bar-group {
    animation-name: growUp;
    animation-duration: $animation-duration;
    animation-delay: 0ms;
    animation-iteration-count: 1;
    opacity: 1;
    animation-direction: forward;
  }

  .inView .Horizontal .bar-measure {
    animation-name: growOut;
    animation-duration: $animation-duration;
    animation-delay: 0ms;
    animation-iteration-count: 1;
    opacity: 1;
  }
}

@mixin main_color($color) {
  .TextCard, .Row {
    border-bottom: 2px solid get-color($name: $color, $variant: "tertiary");
  }
  &.nav-tab-back {
    background-color: get-color($name: $color, $variant: "tertiary");
    border-right: 3px solid get-color($name: $color, $variant: "secondary");
    &.focus {
      background-color: get-color($name: $color, $variant: "secondary");
      border-right: 3px solid get-color($name: $color);
    }
  }
  .annotation {
    color: get-color($name: $color);
  }
  .section-title {
    color: get-color($name: $color);
    border-top: 3px get-color($name: $color) solid;
    border-bottom: 3px get-color($name: $color) solid;
  }
  .text-section {
    color: $text-color;
  }
  [class^="category"] {
    color: get-color($name: $color);

    &:not(.selected) {
      color: get-color($name: $color, $variant: "tertiary");
    }
  }
  &.secondary {
    background-color: get-color($name: $color, $variant: "secondary");
  }
  .bar-measure {
    background-color: get-color($name: $color);
    &.lighter {
      background-color: get-color($name: $color, $variant: "tertiary");
    }
  }
  .inline {
    .bar-measure {
      color: white;
      &.outside-bar > .label-data {
        color: get-color($name: $color);
      }
    }
  }
  .label-subset {
    color: get-color($name: $color);
  }
  .label-group {
    color: get-color($name: $color);
  }
  .slider {
    border: 2px get-color($name: $color) solid;
    &:before {
      background-color: get-color($name: $color);
    }
  }
  hr {
    background: get-color($name: $color, $variant: "tertiary");
  }
  .PieChart {
    .pie-path {
      fill: get-color($name: $color)
    }
  }
  .subtitle {
    background-color: get-color($name: $color);
    border-radius: 2px;
  }
  .SocialButtons {
    .cls-1 {
      fill: get-color($name: $color);
    }
    .copy-text {
      background-color: get-color($name: $color);
    }
  }
  span.legend-dot:before {
    color: get-color($name: $color);
  }
  .WordCloud {
    .list {
      // color: white;
      @each $obj, $color in $primary-palette {
        &.#{$obj} {
          background-color: map-get($color, "tertiary");
        }
      }
    }
  }
}

.orange {
  @include main_color("orange");
  .text-section {
    color: get-color($name: "orange");
  }
  .bar-measure {
    color: $light-text-color
  }
}
.yellow {
  @include main_color("yellow");
  [class^="category"]:not(.selected) {
    color: get-color($name: "yellow", $variant: "quaternary");
  }
  hr {
    background: get-color($name: "yellow", $variant: "quaternary");
  }
}
.green {
  @include main_color("green");
  .bar-measure:not(.lighter) {
    color: $light-text-color;
  }
}
.teal {
  @include main_color("teal");
  .bar-measure:not(.lighter) {
    color: $light-text-color;
  }
}
.blue {
  @include main_color("blue");
}
.periwinkle {
  @include main_color("periwinkle");
  .label-data, .bar-measure {
    color: $light-text-color
  }
  .outside-bar {
    .label-data {
      color: $text-color
    }
  }
}
.pink {
  @include main_color("pink");
  .label-data, .bar-measure {
    color: $light-text-color
  }
}
.red {
  @include main_color("red");
  .label-data, .bar-measure, .bar-measure-parent {
    color: $light-text-color
  }
}
.purple {
  @include main_color("purple");
  .label-data, .bar-measure {
    color: $light-text-color
  }
}
.black {
  @include main_color("black");
  .label-data, .bar-measure {
    color: $light-text-color
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 75px, 0);
    transform: translate3d(0, 75px, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
  }
}

@keyframes growUp {
  0% {
    -webkit-transform-style: preserve-3d;
    transform-origin: 50% 100%;
    transform: scale3d(1, 0, 1);
  }

  to {
    -webkit-transform-style: preserve-3d;
    transform-origin: 50% 100%;
    transform: scale3d(1, 1, 1);
  }
}

@keyframes growOut {
  0% {
    -webkit-transform-style: preserve-3d;
    transform-origin: 0%;
    transform: scale3d(0, 1, 1);
  }

  to {
    -webkit-transform-style: preserve-3d;
    transform-origin: 0%;
    transform: scale3d(1, 1, 1);
  }
}

@keyframes textFade {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media (max-width: $mobile-breakpoint) {
  body {
    min-width: 100%;
  }
  .App {
    // iOS height fix: https://css-tricks.com/the-trick-to-viewport-units-on-mobile/
    height: calc(var(--vh, 1vh) * 100);
    .main {
      grid-template-columns: $mobile-nav-closed-width 1fr;
      height: calc(var(--vh, 1vh) * 100);
    }
  }
}
