@import "../../styling/variables.scss";

.WordCloud {
  margin: auto;

  .parent {
    display: inline-flex;
    flex-wrap: wrap;

    .list {
      flex-grow: 1;
      font-family: "ASAP-Regular";
      font-weight: 600;
      font-size: 1.25em;
      letter-spacing: 0.5px;
      line-height: 2em;
      padding: 2px 10px;
      margin: 8px;
      border-radius: 2px;
      box-shadow: 1px 4px 9px -2.75px $drop-shadow-color;
      transition: transform 300ms, margin 300ms;
      cursor: default;

      &:hover {
        transform: scale(1.15);
      }
    }
  }

}

@media (max-width: $mobile-breakpoint) {
  .WordCloud {
    width: 100%;
  }
}
