@import "../../styling/variables.scss";

.VerticalMany {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  min-height: 200px;
  box-sizing: border-box;
  height: 100%;
  min-width: 200px;
  max-width: 400px;
  font-size: 12px;

  .bars-container {
    display: flex;
    flex-direction: row;
    height: 100%;
    justify-content: center;

    .bar-group {
      height: 100%;
      margin: 0px 10px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      position: relative;

      .bar-label {
        position: absolute;
        transform: translate(-50%, -125%);
        left: 50%;
        width: max-content;
      }

      .bar-measure-parent {
        margin-top: auto;
        position: relative;
        width: $bar-height + 10;

        .bar-measure {
          position: absolute;
          top:0;
          height: 100%;
          width: 100%;
          border-radius: $border-radius;
        }

        span {
          position: absolute;
          top: 0;
          transform: translate(-50%, 5px);
          font-family: Lato-Bold;
        }
      }
    }
  }

  .category-label {
    height: 1em;
    margin-top: 5px;
    font-weight: 600;
  }
}