@import "../../styling/variables.scss";

.PieLegend {
  display: grid;
  grid-template-columns: auto 150px;
  min-height: 150px;
  margin: 10px;
  font-size: 12px;

  .pie-svg {
    grid-column: 1;
    width: 150px;
    height: 150px;

    .pie {
      .slice {
        stroke: white;
        stroke-width: 1px;
      }
    }
  }

  .pie-legend {
    grid-column: 2;
    text-align: left;

    .category {
      font-weight: 700;
      margin-bottom: 1em;
    }

    .legend-row {
      display: flex;
      justify-content: space-between;

      .label {
        .dot {
          display: inline-block;
          width: 10px;
          height: 10px;
          margin-right: 5px;
          border-radius: 50%;
        }
      }

      .value {
        font-weight: 700;
      }
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .PieLegend {
    .pie-svg {
      width: 100%;
    }
  }
}