@import "../../styling/variables.scss";

.Header {
  height: $header-height - 30px;
  width: calc(100vw - 20px);
  transition-property: height margin;
  transition-duration: $section-transition;
  display: flex;
  flex-direction: row;
  align-items: baseline;
  margin: 10px 10px 20px 10px;

  a {
    height: inherit;
    width: fit-content;
    transition: opacity $section-transition;
    opacity: 1;
    .logo {
      height: inherit;
    }
  }

  .title {
    vertical-align: baseline;
    height: fit-content;
    color: get-color();
    font-family: Lato-Bold;
    font-size: 24px;

    span {
      padding: 10px;
    }
  }
}

@media (max-width: $mid-breakpoint) {
  .Header {
    margin: 10px 10px 15px 10px;
    .title { 
      font-size: 16px;
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .Header {
    margin: 10px;
    
    .title {
      display: none;
    }
    &.scrolledPastIntro {
      height: 0;
      padding: 0;
      margin: 0;

      a {
        opacity: 0;
      }
    }
  }
}