@import "../../styling/variables.scss";

.Annotation {
  height: 100%;
  font-family: "Asap-Regular";
  overflow-x: hidden;
  overflow-y: scroll;

  .content {
    text-align: left;
    overflow-y: visible;
    font-size: 1.5em;
    height: 100%;
    display: flex;
    flex-direction: column;

    .highlight {
      font-family: ASAP-Regular;
      .bold {
        font-family: ASAP-Bold;
      }
      &:not(:last-child) {
        padding-bottom: 1em;
      }
    }

    .text {
      font-size: 16px;
      font-family: Lato-Regular;
      margin-bottom: 20px;
      color: $text-color;
      
      .bold {
        font-family: Lato-Black;
      }
    }

    .list {
      padding-bottom: 1em;
      font-family: Lato-Regular;
      font-size: 16px;
      display: list-item;
      margin-left: 25px;
      .bold {
        font-family: Lato-Black;
      }
    }

    .footnote {
      align-self: baseline;
      color: $text-color;
      font-family: Lato-Regular;
      font-size: 12px;
    }
  }
}