@import "../../styling/variables.scss";

.VerticalTwo {
  display: grid;
  grid-template-rows: minmax(200px, 100%) minmax(2em, auto);
  row-gap: 10px;
  min-height: 300px;
  box-sizing: border-box;
  min-width: 250px;
  max-width: 400px;
  font-size: 12px;
  margin: 0 auto;

  .bars-container {
    grid-row: 1;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: auto auto;
    height: 100%;
    justify-content: center;

    .bar-group {
      height: 100%;
    }

    .bar-group.left {
      display: grid;
      grid-template-columns: minmax(min-content, 80px) $bar-width;
      grid-gap: 10px;

      div {
        margin-top: auto;
      }
      
      .bar-label {
        grid-column: 1/2;
        text-align: right;
      }
    } 

    .bar-group.right {
      display: grid;
      grid-template-columns: $bar-width minmax(min-content, 80px);
      grid-gap: 10px;
      
      div {
        margin-top: auto;
      }
      
      .bar-label {
        grid-column: 2/3;
        grid-row: 1;
        text-align: left;
      }
    } 

    .bar-measure {
      width: $bar-width;
      border-radius: $border-radius;
      position: relative;

      span {
        font-family: Lato-Black;
        position: absolute;
        top: 5px;
        left: 50%;
        transform: translate(-50%);
      }
    }
  }

  .category-label {
    grid-row: 2;
    margin-top: 5px;
    font-weight: 600;
    max-width: 300px;
    margin: 0 auto;
  }
}