@import "../../styling/variables.scss";

.Horizontal {
  display: grid;
  column-gap: 30px;
  row-gap: 10px;
  max-width: $card-max-width;
  flex-grow: 1; // more conservative alternative to width: 100%;

  /* 2021: column positioning
      This allows for grouped horizontal bars which were two toned
      to take up two columns. The nth-child(1) comes second so that if there 
      is only age or gender identity, it always floats to the top, while race 
      and ethnicity is always to the right.
    */
    &.columns {
      grid-template-columns: 1fr 1fr;
      .label-group-parent {
        &:nth-child(2),
        &.by-age {
          grid-row: 2/3;
          grid-column: 1/2;
        }
  
        &:nth-child(1), 
        &.by-gender-identity {
          grid-row: 1/2;
          grid-column: 1/2;
        }
  
        &.by-race-ethnicity {
          grid-row: 1/3;
          grid-column: 2/end;
        }
      }
    }
    /* end column posiitoning */
  
  .label-group-parent {
    display: grid;
    grid-row-gap: 2px;
    min-width: calc(50% - 5px);
    height: fit-content;

    .label-group-div {
      max-height: 15px;
      align-self: center;
      position: relative;
      hr {
        height: 2px;
        border: none;
      }
      .label-group {
        position: absolute;
        background-color: white;
        height: 10px;
        left: 0;
        top: 0;
        padding: 0 5px 0 0;
        font-size: 12px;
        font-weight: 600;
        font-family: Lato-Bold;
        text-align: right;
        z-index: 1;
      }
    }
  
    .bar-group {
      position: relative;
      display: grid;
      height: $bar-height;
  
      // this applies the same border style to both .dots-group and .bar for spacing / centering reasons
      div:not(.label-inline) {
        border-radius: $border-radius;
      }
  
      .bar-dots {
        margin: auto;
        border:none;
        border-top: 6px dotted $light-grey;
        background-color:#fff;
        width: 100%;
      }
  
      .bar-measure {
        position: absolute;
        height: $bar-height;
        bottom: 0px;
        left: -2px;
        display: grid;
        grid-auto-columns: auto 45px;
        align-content: center;
        transition: width 500ms ease, opacity 300ms;
        &.outside-bar {
          .label-data {
            position: relative;
            width: inherit;
            margin: 5px;
          }
        }
      }
  
      .label-data {
        grid-row: 1;
        font-size: 12px;
        font-family: Lato-Black;
        border: none;
        text-align: right;
        margin: 0px 10px;
        pointer-events: none;
      }
    }

    .label-data-name {
      pointer-events: none;
      border: none;
      line-height: 10px;
      font-size: 12px;
      text-align: right;
      padding: 3px 0;

      &.inside {
        font-family: Lato-Bold;
        color: white;
        text-align: left;
        transform: translate(0px, $bar-height - 5);
        z-index: 1;
        margin: 0 5px;
      }
    }
  }

  &.inline {
    min-width: 400px;

    .label-group-parent {
      grid-row-gap: 5px;

      .label-group-div {
        height: 20px;
        margin-bottom: 10px;
      }
      .bar-measure {
        display: flex;
        justify-content: space-between;

        div {
          margin: auto 5px;
        }

        .label-data-name {
          font-family: Lato-Bold;
          text-align: left;
        }
      }
    }
  }
}

@media (max-width: $mobile-breakpoint) {
  .Horizontal.inline {
    min-width: 100%;

    .outside-bar > .label-data {
      width: fit-content;
    }
  }
}

