@import "../../styling/variables.scss";

.graph-title {
  color: $text-color;
  text-align: left;
  padding-bottom: 1em;

  span.legend-text {
    display: block;
    font-family: Lato-Regular;
    font-size: 16px;
  }

  span.legend-dot {
    &:before {
      font-size: 28px;
      line-height: 18px;
      content: "\2022";
      position: relative;
      top: 3px;
    }
}

  span.legend-dot:last-child:before {
    opacity: $grouped-opacity;
  }
}

@media (max-width: $mobile-breakpoint) {
  .graph-title {
    margin: 0 15px;
  }
}
