@import "../../styling/variables.scss";


.Row {
  display: flex;
  flex-wrap: wrap;
  gap: 1em;
  padding: 2em 2px;

  .RowItem {
    flex-grow: 1;
    flex-basis: 0; // even sizing
    min-height: 100px;
    min-width: 300px;
    height: auto;
    gap: 1em;

    display: flex;
    flex-direction: column;
    box-sizing: border-box;

    .Card {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      position: relative;
      text-align: center;
      box-sizing: border-box;
      padding-right: 10px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 30px;
    }

    .Card > div {
      height: auto;
    }

  }

  &:last-child {
    border-bottom: none;
    margin-bottom: 2px;
  }

}

@media (max-width: $mobile-breakpoint) {
  .Card {
    gap: 0;
    padding: 1em;
    height: unset;

    .chart-wrapper {
      .VerticalBarChart,
      .HorizontalBarChart {
        max-height: $mobile-card-height;
        min-height: $mobile-chart-min-height;
      }

      &:not(.annotation) {
        grid-auto-rows: auto auto $mobile-chart-min-height;
      }

      .graph-title {
        height: auto;
        padding: 10px 0px;
      }
    }
  }
}
